@font-face {
    font-family: "Agency FB Bold";
    src: url(AGENCYB.TTF) format("truetype");
}

.thin {
    margin: 0px;
    padding: 0px;
}

.black-on-blue {
    background-color: #00B0F0;
    color: black;
}

.DefaultLayout {
    min-height: 100vh;
    background: black;
    color: white;
}

.page-content {
    font-family: Helvetica, Arial, sans-serif;
    margin: 10px;
    justify-self: center;
    text-align: center;
    flex-grow: 1;
}


.site-header {
    padding-top: 10px;
    background-color: #00B0F0;
    color: black;
}

.site-footer {
    padding-top: 1vh;
    padding-bottom: 1vh;
    font-family: Helvetica, Arial, sans-serif;
    text-align: center;
}

.cahaba-logo {
    font-size: 15vh;
    font-family: "Agency FB Bold";
    text-align: center;
}
.cahaba-logo a {
    color: inherit;
    text-decoration: inherit;
}


.menu-container {
    margin: auto;
    padding: 0px;
}

.menu-box {
    font-family: "Agency FB Bold", sans-serif;
    font-size: 5vh;
    display: block;
    padding: 0px;
    margin-left: 10px;
    margin-right: 10px;
}

.black-white-link {
    text-decoration: none;
    color: black;
}
.black-white-link:hover {
    text-decoration: none;
    color: white;
}

.horizontal-flex-container {
    display: flex;
    flex-direction: row;
}

.vertical-flex-container {
    display: flex;
    flex-direction: column;
}

.vertical-center-flex-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
}